
.rating{
    position: relative;
    font-size: 24px;
    display: inline-block;
    margin-left:50px;
    &::before{
            content:"★★★★★";
            display: block;
            }
        &__items{
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            display: flex;
            flex-direction: row-reverse;
            overflow: hidden;
        }
&__item{
    position: absolute;
    width: 0%;
    height: 0%;
    opacity: 0;
    visibility: hidden;
    top: 0;
    left: 0;
}
&__label{
    flex:0 0 20%;
    height: 100%;
    cursor: pointer;
    color:$accentColor;
    &::before{
        content:"★";
        display: block;
        transition: color 0.3s ease 0s;
        -webkit-transition: color 0.3s ease 0s;
        -moz-transition: color 0.3s ease 0s;
        -ms-transition: color 0.3s ease 0s;
        -o-transition: color 0.3s ease 0s;
}
}

&__item:checked,
&__item:checked ~ &__label{
    color:$accentColor;
}
&__label:hover,
&__label:hover ~ &__label,
&__label:checked ~ &__label:hover{
    color:$accentColor;
}

}








